h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--white);
  font-family: DMSans-400, sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 100%;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@font-face {
  font-family: DMSans-400;
  src: url("DMSans-Regular.23c41658.ttf");
}

@font-face {
  font-family: DMSans-400-italic;
  src: url("DMSans-Italic.b1eebbd6.ttf");
}

@font-face {
  font-family: DMSans-500;
  src: url("DMSans-Medium.0957dae6.ttf");
}

@font-face {
  font-family: DMSans-700;
  src: url("DMSans-Bold.51beb29d.ttf");
}

::-webkit-scrollbar {
  width: 8px;
  height: 167px;
}

::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 12px;
}

:root {
  --deep-black: #111;
  --soft-black: #202024;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --backdrop-color: #1116;
  --backdrop-color-sl: #f6f6f6;
  --shopping-list-text-color: #0009;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 2px;
  --theme-switcher-gradient: linear-gradient(#4f2ee8, #fff);
  --text-categories: #1119;
}

body[data-theme="light"] {
  --deep-black: #111;
  --soft-black: #202024;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --backdrop-color: #1116;
  --backdrop-color-sl: #f6f6f6;
  --bookcard-backdrop-color: #fff;
  --shopping-list-text-color: #0009;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 2px;
  --theme-switcher-gradient: linear-gradient(#4f2ee8, #fff);
  --categories-text: #1119;
  --book-card-filter: brightness(0) invert(0);
  --modal-icon-color: grayscale(1);
}

body[data-theme="dark"] {
  --deep-black: #fff;
  --soft-black: #202024;
  --purple: #eac645;
  --yellow: #4f2ee8;
  --white: #202024;
  --backdrop-color: #1116;
  --backdrop-color-sl: #202024;
  --bookcard-backdrop-color: #111;
  --shopping-list-text-color: #fff9;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 17px;
  --theme-switcher-gradient: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --categories-text: #fff9;
  --book-card-filter: brightness(0) invert(1);
  --modal-icon-color: brightness(0) invert(1) grayscale(1) brightness(1) contrast(100%);
}

:root {
  --regular: font-weight: 400;
  --medium: font-weight: 500;
  --bold: font-weight: 700;
  --function-duration: .35s;
  --function-timing: cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  border: 2px solid var(--deep-black);
  z-index: 3;
  background-color: var(--white);
  border-radius: 0 0 8px 8px;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 64px;
  margin: 0 auto;
  padding: 18px 20px;
  display: flex;
  position: relative;
  top: 0;
}

.user-photo-img {
  border-radius: 50%;
  width: 37px;
  height: 37px;
  margin-right: 12px;
}

.logo {
  background: var(--icon-logo);
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: 109px;
  height: 28px;
  margin-right: auto;
  font-weight: 600;
  display: flex;
}

.logo a {
  width: 109px;
  height: 28px;
}

.header-title {
  font-family: DMSans-400, sans-serif;
}

.header-wrap {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.theme-switcher {
  background-image: var(--theme-switcher-gradient);
  cursor: pointer;
  border-radius: 50px;
  align-items: center;
  width: 39px;
  height: 22px;
  margin-right: 14px;
  padding: 1px 0 1px 1px;
  display: flex;
  position: relative;
}

.theme-switcher:before {
  content: "";
  transform: translateX(var(--switch-circle-pos));
  background-color: #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: transform .25s;
  position: absolute;
  box-shadow: 1px 1px 2px #0000001a;
}

.mob-menu-open, .mob-menu-close {
  stroke: var(--deep-black);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
}

.header-user-profile, .header-nav {
  display: none;
}

.username {
  justify-content: center;
  width: 120px;
  height: 29px;
  font-family: DMSans-700, sans-serif;
  font-size: 22px;
  line-height: 28.64px;
}

.mob-menu.visually-hidden {
  transform: translateY(100%);
}

.mob-menu {
  z-index: 2;
  background-image: linear-gradient(#4f2ee8, #fff);
  background-position: bottom;
  background-repeat: repeat-x;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 111%;
  transition: transform .25s linear;
  display: flex;
  position: absolute;
  top: 0;
  overflow-y: auto;
  transform: translateY(0%);
}

.mob-menu-wrap {
  flex-direction: column;
  display: flex;
  overflow-y: auto;
}

.user-profile {
  flex-direction: row;
  align-items: center;
  width: 184px;
  height: 48px;
  margin-top: 88px;
  display: flex;
}

.mob-menu-sign-up-btn {
  cursor: pointer;
  color: #fff;
  background-color: #0000;
  border: 2px solid #111;
  border-radius: 18px;
  align-items: center;
  width: 184px;
  height: 48px;
  margin-left: 20px;
  padding: 0;
  display: flex;
  position: relative;
}

.username {
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  line-height: calc(18 14);
  width: 50px;
  height: 18px;
  margin-left: 14px;
  margin-right: 82px;
  font-weight: 500;
}

.mob-menu-sign-up-btn svg {
  width: 20px;
  height: 20px;
  margin-right: 4px;
}

.user-photo {
  stroke: #fff;
}

.mob-menu-user-btn {
  cursor: pointer;
  color: #fff;
  background-color: #0000;
  border: none;
  border-radius: 18px;
  align-items: center;
  width: 184px;
  height: 45px;
  margin-left: 20px;
  padding: 0;
  display: flex;
  position: relative;
}

.mob-menu-user-btn .username {
  margin-right: 0;
  font-size: 22px;
  line-height: 28px;
}

.user-photo-bg {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  margin-left: 12px;
  display: flex;
}

.mob-menu-nav {
  flex-direction: column;
  row-gap: 24px;
  margin: 266px auto 0;
  display: flex;
}

.mob-menu-nav-item-home {
  justify-content: center;
  align-items: center;
  width: 72px;
  height: 34px;
  list-style: none;
  display: flex;
}

.mob-menu-nav-item-shopping-list {
  justify-content: center;
  align-items: center;
  width: 156px;
  height: 34px;
  list-style: none;
  display: flex;
}

.mob-menu-home-link {
  color: #fff;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
}

.mob-menu-shopping-list-link {
  color: #fff;
  flex-direction: row;
  align-items: center;
  gap: 4px;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.mob-menu-log-out-btn {
  color: #111;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  width: 118px;
  height: 48px;
  margin-bottom: 72px;
  margin-left: 20px;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.mob-menu-log-out-btn svg {
  width: 20px;
  height: 20px;
}

.mob-menu-img {
  z-index: -1;
  position: absolute;
  bottom: 0;
}

.is-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.is-active {
  background-color: #eac645;
  border-radius: 36px;
  font-weight: 700;
}

.is-active a {
  color: #111;
}

.null {
  display: none;
}

@media screen and (min-width: 375px) {
  .mob-menu-wrap, .mob-menu-log-out-wrap {
    width: 100%;
    margin: 0 auto;
  }

  .mob-menu-img {
    display: none;
  }
}

