body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.categories-container {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 226px;
  padding: 0;
  display: flex;
  overflow: hidden;
}

.categories-2 {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.categories-title {
  color: var(--categories-text);
  letter-spacing: -.36px;
  font-family: DMSans-400;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

.categories-list {
  width: 100%;
  margin: 48px 0 0;
  padding: 8px 0 10px;
  list-style: none;
  overflow: auto;
}

.categories-el, .categories-btn, .categories-list li {
  color: var(--categories-text);
  letter-spacing: -.32px;
  white-space: normal;
  text-overflow: ellipsis;
  cursor: pointer;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  max-width: 100%;
  font-family: DMSans-400;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  list-style: none;
  transition: color .3s, font-size .3s;
  display: flex;
  overflow: hidden;
}

.categories-el:hover, .categories-btn:hover, .categories-list li:hover, .categories-el.active, .categories-list li.active {
  color: var(--purple);
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DMSans-700;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

.categories-btn {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

.categories-title.active {
  color: var(--purple);
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DMSans-700;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

@media (min-width: 320px) and (max-width: 767px) {
  .categories-el {
    white-space: normal;
    text-overflow: ellipsis;
    max-width: 100%;
    margin-bottom: 24px;
    font-size: 16px;
    overflow: hidden;
  }

  .categories-el:hover, .categories-list li:hover, .categories-el.active {
    max-width: calc(100% - 16px);
    font-size: 16px;
  }
}

@media screen and (min-width: 768px) {
  .categories-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    width: 309px;
    height: 472px;
    display: flex;
  }

  .categories-el {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .categories-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    width: 328px;
    display: flex;
  }

  .categories-el {
    color: var(--categories-text);
    letter-spacing: -.36px;
    margin-bottom: 32px;
    font-family: DMSans-400;
    font-size: 18px;
    font-style: normal;
    line-height: 24px;
  }

  .categories-list {
    width: 100%;
    padding: 0;
    list-style: none;
    overflow: auto;
  }

  .categories-el:hover, .categories-list li:hover, .categories-el.active {
    color: var(--purple);
    letter-spacing: -.36px;
    text-transform: uppercase;
    font-family: DMSans-700;
    font-size: 18px;
    font-style: normal;
    line-height: 24px;
  }
}

.charity-box {
  padding-inline: 40px;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 335px;
  margin: 0 auto;
  padding-top: 24px;
  padding-bottom: 20px;
  position: relative;
  box-shadow: 0 10px 10px #f5f5f71a;
}

.charity-header {
  gap: 12px;
  margin-bottom: 40px;
  display: flex;
}

.charity-title {
  color: #fff;
  letter-spacing: -1.12px;
  font-family: DMSans-700;
  font-size: 28px;
  line-height: 1.14;
  font-weight: var(--bold);
}

.swiper-slide:before {
  counter-increment: section;
  content: "0" counter(section) " ";
  color: #fff;
  font-family: DMSans-400;
  font-size: 14px;
  font-weight: var(--regular);
  letter-spacing: -.56px;
  line-height: 1.29;
}

.swiper {
  height: 188px;
  margin-bottom: 48px;
  overflow: hidden;
}

.swiper-wrapper {
  counter-reset: section;
  width: 100%;
  height: 100%;
  margin-bottom: 16px;
  list-style-type: none;
}

.swiper-slide {
  align-items: center;
  gap: 16px;
  width: 50%;
  font-family: DMSans-400;
  font-size: 18px;
  display: flex;
}

.charity-link {
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.charity-img {
  filter: brightness(0) invert();
  max-width: -moz-fit-content;
  max-width: fit-content;
  max-height: 100%;
}

.swiper-button-next, .swiper-button-up {
  z-index: 0;
  background-color: #fff;
  border: none;
  border-radius: 38px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.swiper-button-next-icon, .swiper-button-up-icon {
  fill: #4f2ee8;
}

.swiper-button-next-icon {
  transform: rotate(180deg)translateY(-105%)translateX(-105%);
}

.swiper-button-up.is-hidden {
  display: none;
}

@media screen and (max-width: 375px) {
  .charity-title {
    font-size: 25px;
  }

  .charity-box {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .swiper-button-next, .swiper-button-up {
    width: 38px;
    height: 38px;
  }

  .charity-box {
    margin: 0;
    padding-bottom: 14px;
  }

  .swiper-wrapper {
    margin-bottom: 32px;
  }

  .swiper {
    height: 292px;
    margin-bottom: 70px;
  }
}

@media screen and (min-width: 1440px) {
  .charity-box {
    width: 356px;
    padding-bottom: 24px;
  }

  .swiper-wrapper {
    margin-bottom: 40px;
  }

  .swiper {
    margin-bottom: 78px;
  }
}

.home-book-image-container {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.home-book-overlay {
  background-color: #eac645;
  border-radius: 0 0 8px 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 0;
  transition: height .3s;
  display: flex;
  position: absolute;
  bottom: 14px;
  left: 0;
  overflow: hidden;
}

.home-book-card:hover .home-book-overlay, .home-card:hover .home-book-overlay {
  height: 70px;
}

.home-book-image {
  border-radius: 8px;
  margin-bottom: 14px;
}

.home-book-content {
  letter-spacing: -.04em;
  text-transform: uppercase;
  font-family: DMSans-500;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
}

.section-heading {
  margin-bottom: 40px;
  font-family: DMSans-700;
  font-size: 32px;
  line-height: 1.2;
  display: block;
}

.home-bestsellers-heading, .category-name-heading {
  color: var(--deep-black);
  letter-spacing: -1.28px;
  font-size: 32px;
  line-height: 1.2;
}

.home-book-card {
  flex-direction: column;
  display: flex;
}

